.Pokecard{
    /* border: 2px solid magenta; */
    width: 210px;
    padding: 10px;
    margin: 1rem;
    box-shadow: 7px 10px 12px -5px rgba(0, 0, 0, 0.56);
    text-align: center;
    background-color: white;
    border-radius: 12px;
}

.Pokecard-data{
    font-size: 80%;
    
}

.Pokecard img{
    transition: all ease 500ms;
}

.Pokecard img:hover{
    transform: scale(1.25);
}